'use client';

import { useState } from 'react';
import LeftMenu from './LeftMenu';

const contentMap: Record<string, { title: string; description: string }> = {
  'grade-trend': {
    title: '교과별 등급추이',
    description: '교과별 등급 추이를 확인할 수 있습니다.',
  },
  'subject-grade': {
    title: '교과성적',
    description: '교과 성적을 확인할 수 있습니다.',
  },
  'learning-development': {
    title: '교과학습발달',
    description: '교과 학습 발달 상황을 확인할 수 있습니다.',
  },
  'creative-activity': {
    title: '창의적 체험활동',
    description: '창의적 체험활동 내역을 확인할 수 있습니다.',
  },
  'behavior-opinion': {
    title: '행동특성 및 종합의견',
    description: '행동특성 및 종합의견을 확인할 수 있습니다.',
  },
  attendance: {
    title: '출결/자격증/봉사',
    description: '출결, 자격증, 봉사활동 내역을 확인할 수 있습니다.',
  },
};

const ConsultDetail = () => {
  const [activeSection, setActiveSection] = useState('subject-grade');
  const content = contentMap[activeSection];

  return (
    <div className='flex gap-[40px] max-md:flex-col max-md:gap-[24px]'>
      <aside className='w-[200px] flex-shrink-0 max-md:w-full'>
        <LeftMenu
          activeSection={activeSection}
          onSectionChange={setActiveSection}
        />
      </aside>
      <main className='flex-1'>
        <h1 className='text-[26px] font-[700] leading-[1.3] text-[var(--n-800)] mb-[24px]'>
          {content.title}
        </h1>
        <div className='bg-white rounded-[8px] border border-[var(--n-200)] p-[24px]'>
          <p className='text-[15px] text-[var(--n-600)]'>{content.description}</p>
        </div>
      </main>
    </div>
  );
};

export default ConsultDetail;
